  function readMore(jObj, lineNum) { //function
    if ( isNaN(lineNum) ) {
      lineNum = 4;
    }
    var go = new ReadMore (jObj, lineNum);
  }
  function ReadMore(_jObj, lineNum) { //class
    var READ_MORE_LABEL = 'показать полностью';
    var HIDE_LABEL = 'свернуть';

    var jObj = _jObj;
    var textMinHeight = ''+ (parseInt(jObj.children('.spoiler__text').css('line-height'),10)*lineNum) +'px';
    var textMaxHeight = ''+jObj.children('.spoiler__text').css('height');

    jObj.children('.spoiler__text').css('height', ''+ textMaxHeight);
    jObj.children('.spoiler__text').css( 'transition', 'height .5s');
    jObj.children('.spoiler__text').css('height', ''+ textMinHeight);

    jObj.append ('<div class=spoiler__trigger>'+READ_MORE_LABEL+'</div>');

    jObj.children('.spoiler__trigger').click ( function() {
      if (jObj.children('.spoiler__text').css('height') === textMinHeight) {
        jObj.children('.spoiler__text').css('height', ''+textMaxHeight);
        jObj.children('.spoiler__trigger').html(HIDE_LABEL).addClass('active');
      } else {
        jObj.children('.spoiler__text').css('height', ''+textMinHeight);
        jObj.children('.spoiler__trigger').html(READ_MORE_LABEL).removeClass('active');
      }
    });
  }

  readMore( $('.spoiler'), 7);

