function bindLayout() {
  var buttons = $('.layout-switchers .btn'),
    layout = $('.items-layout');
  buttons.on('click', function() {
    var $this = $(this);
    buttons.removeClass('active');
    if($this.data('layout') === 'linear') {
      layout.removeClass('blocky').addClass('linear');
      $this.addClass('active');
      layout.find('.col-auto').css('width', '100%');
    }
    if($this.data('layout') === 'blocky') {
      layout.removeClass('linear').addClass('blocky');
      $this.addClass('active');
      layout.find('.col-auto').css('width', 'auto');
    }
  });
}
bindLayout();

